import React, {useState} from 'react'
import Button from './components/Button'
import './index.css'

const database = {
    username: 'kehinde',
    password: '00711'
}


const HomePage = () => {
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [showError, setError] = useState()

    const [getValue, setValue] = useState(0)

    const increment = () => {
        setValue(getValue + 1)
    }

    const decrement = () => {
        setValue(getValue - 1)
    }

    const submit = () => {
        if(username !== database.username && password !== database.password){
            setError('You have input wrong password or wrong username')
        }else {
            window.location.href = '/feeds'
        }
    }

    return (
        <div className="main_container">
            <div>{getValue}</div>
            <div className="row">
                <Button value="Increment" onClick={() =>increment() } />
                <Button value="Decrement" onClick={() => decrement()} />
            </div>
            <div className="form_Container">
                <div className="textInputContainer">
                    <input type="text" className="textInput" placeholder="Username" onChange={(e) => setUsername(e.target.value)} />
                </div>
                <div className="textInputContainer">
                     <input type="password" className="textInput" placeholder="Password" onChange={(e) => setPassword(e.target.value)} />
                </div>
                <div className="textInputContainer" onClick={() => submit()}>
                    <div className="submitButton">Login!</div>
                </div>
            </div>
            {showError && <div>{showError}</div>}
            <div className="links">
                <div className="adds">check what i got</div>
                <a href="/figma_structure">
                    <div className="first_button_figmal">Figma Structure</div>
                </a>
                <a href="/chats">
                <div className="second_button_chat">chart created</div>
                </a>
            </div>
        </div>
    )
   
}

export default HomePage

